@import url(https://fonts.googleapis.com/css?family=Noto+Serif+SC);
@import url(https://fonts.googleapis.com/css?family=Pacifico);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html{
  background-color: #e7dfdd;
}
.about_me_desc {
  padding-top: 3vh;
}
/* img {

} */
.responsive {
  max-width: 100%;
  width: 550px;
  height: 350px;
  margin-bottom: 10px;
  float: left;
  padding-right: 1em;
}
.description {
  padding-top: 1vh;
  margin: 1em;
}
.intro{
  margin-bottom: 6px;
  margin-top: 0px;
  line-height: 22px;
}

.intro1{
  margin-top: 5px;
  margin-bottom: -25px;

}

.skills{
  display: inline-flex;
  flex-wrap: wrap;
  max-width: 40vw;
  justify-content: center;
  padding-top: 5px;
}
ul.skills li{
  list-style: none;
  border: 1px solid #373737;
  margin: .3em;
  padding: .2em;
  box-shadow: 5px 5px #C0B283;

}
@media only screen and (max-width: 1055px) {
  .description {
    padding-top: .5vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;  
  }
  .intro{text-align: center}
  .skills{
    display: inline-flex;
    flex-wrap: wrap;
    max-width: 90vw;
    justify-content: center;
  }

}

@media only screen and (max-width: 939px) {
  .about_me{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .responsive {
    max-width: 100%;
    width: 550px;
    height: 350px;
    padding-right: 1em;
  
  }
  .description {
    padding-top: .5vh;
  
  }
  .intro{text-align: center}

}

html{
  background-color: #e7dfdd;
}
.body {
  padding: 0.6em;
}
h2 {
  text-align: center;
}
.mywork_whole {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  background-color: white;
}
.responsive1,
.responsive2 {
  width: 100%;
  height: 325px;
  margin-bottom: 10px;
  /* float: left; */
  /* float: right; */
  margin-left: auto;
  margin-right: auto;
}

.mywork_whole > div {
  display: flex;
  flex-direction: column;
  width: 47vw;
  margin-right: 0.6em;
  margin-bottom: 0.6em;
  padding: 0.2em;
  min-width: 375px;
}
.work_images {
  width: 55vw;
  padding: 0.2em;
}

.my_work3_text {
  width: 30vw;
}

.my_work3 {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

@media only screen and (max-width: 918px) {
  .mywork_whole > div {
    display: flex;
    flex-direction: column;
    width: 90vw;
    min-width: 375px;
  }
  .responsive1 {
    max-width: 100%;
    height: 400px;
    margin-bottom: 10px;
    margin-left: auto;
    margin-right: auto;
	}
	
}

@media only screen and (max-width: 750px) {
  .mywork_whole > div {
    display: flex;
    flex-direction: column;
    width: 90vw;
    min-width: 375px;
  }
  .responsive1 {
    max-width: 100%;
    height: 350px;
    margin-bottom: 10px;
    margin-left: auto;
    margin-right: auto;
  }
  .my_work3 {
    display: flex;
		flex-direction: column;
		justify-items: center;
  }
  .responsive2 {
    width: 90vw;
    height: 350px;
	}
	.my_work3_text {
		width: 95vw;
	}
}

@media only screen and (max-width: 650px) {
  .mywork_whole > div {
    display: flex;
		flex-direction: column;
		justify-content: center;
    width: 90vw;
    min-width: 375px;
  }
  .responsive1 {
    max-width: 100%;
    height: 275px;
    margin-bottom: 10px;
    margin-left: auto;
    margin-right: auto;
	}
	.my_work3_text {
		width: 95vw;
	}
}

